.newsletter{
  text-transform: uppercase;
  font-family: "Rubik";
  letter-spacing: 1px;
  display: flex;

  &__form-container{

    &__name{
      display: inline-block;
      margin-right: 20px;
    }

    &__input-container{
      display: inline-block;
  
      &__input, &__btn{
        display: inline-block;
        padding: 5px;
        border-radius: 6px;
        margin: 0px;
      }
    }
  }
}